/* Main.css */

/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&family=Roboto:wght@400;500&display=swap');

.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to bottom right, #005f73, #0a9396, #94d2bd, #ca6702);
  color: white;
  font-family: 'Roboto', sans-serif;
  padding: 16px;
}

.heading {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 16px;
  opacity: 0;
  transform: translateY(-50px);
  animation: fadeInUp 1s forwards;
  font-family: 'Poppins', sans-serif;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2); /* Add shadow for depth */
}

.heading2 {
  font-size: 2.8rem;
  font-weight: bold;
  margin-bottom: 24px;
  letter-spacing: 2px;
  opacity: 0;
  transform: translateY(50px);
  animation: fadeInDown 1s 0.5s forwards;
  font-family: 'Poppins', sans-serif;
}

.heading2 span {
  color: #fbbf24; /* Yellowish color */
}

.search-container {
  display: flex;
  border: 2px solid white;
  border-radius: 9999px;
  overflow: hidden;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
  transform: scale(0.8);
  animation: scaleIn 0.5s 1.2s forwards;
}

.search-container input {
  padding: 12px;
  font-size: 1rem;
  color: black;
  border: none;
  outline: none;
  width: 250px;
}

.search-container button {
  background-color: white;
  color: black;
  padding: 12px;
  border: none;
  cursor: pointer;
}

.peacock {
  font-size: 5rem;
  animation: rotatePeacock 3s infinite;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInDown {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  to {
    transform: scale(1);
  }
}

@keyframes rotatePeacock {
  0% {
    transform: rotate(-10deg);
  }
  50% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(-10deg);
  }
}

/* Animations */
@keyframes slideUp {
    to {
      transform: translateY(-80px);
      opacity: 1;
    }
  }
  
  @keyframes shrinkHeading {
    to {
      font-size: 1.5rem;
      margin-bottom: 0;
    }
  }
  
  @keyframes shrinkSearch {
    to {
      transform: scale(0.7);
    }
  }
  
  /* Navbar Transition Classes */
  .shrink-header .header {
    animation: slideUp 0.5s forwards;
  }
  
  .navbar {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #005f73;
    padding: 10px 20px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  
  .heading-small {
    font-size: 1.5rem;
    margin: 0;
    animation: shrinkHeading 0.5s forwards;
  }
  
  .heading2-small {
    font-size: 1.2rem;
    margin: 0;
    animation: shrinkHeading 0.5s forwards;
  }
  
  .search-navbar {
    margin: 0;
    transform: scale(0.8);
    animation: shrinkSearch 0.5s forwards;
    box-shadow: none;
  }
  
  .search-navbar input {
    width: 200px;
  }
  
